<template>
	<div class="set_nickname">
    <van-nav-bar
        title="银行卡认证"
        left-text="返回"
        right-text=""
        left-arrow
        @click-left="goBack"
    />
    <van-cell-group>
      <van-cell title="认证状态">{{getDesc()}}</van-cell>
      <van-cell v-if="errorMsg && status == 3" style="color:red;" title="审核提示">{{errorMsg}}</van-cell>
    </van-cell-group>
		<van-cell-group>
			<van-field v-model="real_name" label="姓名" :disabled="flag" :placeholder="real_name ? '' : '请填写您银行卡姓名'" :error="!!$vuelidation.error('real_name')"/>
      <van-field v-model="bank_name" label="开户银行" :disabled="flag" :placeholder="bank_name ? '' : '请选择开户银行'" readonly @click="showBank = true" :error-message="bank_prompt" :error="!!$vuelidation.error('bank_name')"/>
      <van-field v-model="bank_number" label="银行卡号" :disabled="flag" :placeholder="bank_number ? '' : '请填写银行卡号' " :error="!!$vuelidation.error('bank_number')"/>
		</van-cell-group>

		<div class="bottom_btn">
			<van-button v-if="status==0 || status==3" size="large" type="danger" @click="save">保存</van-button>
		</div>

    <van-popup v-model="showBank" position="bottom">
      <van-picker
        show-toolbar
        :columns="bankColumns"
        title="选择银行"
        @cancel="showBank = false"
        @confirm="onBankConfirm"/>
    </van-popup>
	</div>
</template>


<script>
  import{Cell, CellGroup, Popup, Picker, Button, Field, Toast} from 'vant';
	import { USER_BINDBANK, USER_SENDCODE } from '@/api/user';
  import bankList from '../../../../assets/data/bank';
  const bankColumns = bankList['bank_list'];

	export default {

	  components:{
	    [CellGroup.name]:CellGroup,
      [Cell.name]:Cell,
      [Popup.name]:Popup,
      [Picker.name]:Picker,
      [Button.name]:Button,
      [Field.name]: Field
    },

		data(){
			return {
				real_name: '',
        bank_name : '',
        bank_number : '',
        errorMsg : '',
        bankColumns,
        showBank : false,
				bank_prompt: '',
        flag: false,
        status:"",
			}
		},

    computed: {
			prompt() {
				return this.bank_name ? '' : '请务必填写真实姓名以及对应的银行卡号，否则无法正常收款！'
			}
    },

		vuelidation: {
			data: {
        bank_name: {
					required: {
						msg(){
							return "请选择开户银行"
						}
					}
				},
        bank_number: {
          required: {
            msg(){
              return "请填写银行卡号"
            }
          }
        },
			}
		},

		mounted(){
			this.getBank();
		},

		methods: {
      getBank(){
        let _this = this;
        _this.$user.getUserInfo(function(data){
          _this.status= data.bank_status;
          _this.real_name = data.name;
          _this.bank_name = data.bank;
          _this.bank_number =data.bank_number;
          _this.errorMsg =data.bank_remark;
          if (_this.bank_name && _this.bank_number && _this.real_name) {
            _this.flag = true;
            _this.showCode = false;
          }
        });
			},
      getDesc() {
        switch (this.status) {
          case 0:
            return "待提交";
          case 1:
            return "待审核";
          case 2:
            return "已审核通过";
          case 3:
            return "审核不通过";
        }
      },
			save(){
				if(this.mixValid()){
					this.$reqPost(USER_BINDBANK, {
						"bank_name": this.bank_name,
						"bank_number": this.bank_number,
					}).then((res) => {
					  let result = res.data;
					  if(result.code!=0){
					      Toast.fail(result.msg);
					      return;
            }
						this.$router.go(-1);
						return this.$dialog.alert({message: '保存成功'})
					}).catch((err) => {
						// console.log(err)
					});
				}
			},

      onBankConfirm(value, index){
        this.showBank = false;
        this.bank_name = value.text;
			},

			goBack() {
				this.$router.go(-1);
			}
		}
	}
</script>


<style scoped>
	.bottom_btn{
		padding: 30px 15px 0 15px;
	}
</style>



